<script>
import dayjs from 'dayjs'

export default {
  data() {
    return {
      top_level: []
    }
  },
  methods: {
    thisData() {
      require('dayjs/locale/zh-cn')
      return dayjs().locale('zh-cn').format('YYYY-MM-DD dddd')
    },
    async getTopLevel() {
      let res = await this.$api.getTopNav();
      this.top_level = res.data.top_level;
    }
  },
  mounted() {
    this.getTopLevel()
  }
}
</script>

<template>
  <div class="Home">
    <header class="header">
      <div class="header-content">
        <div class="header-time">今日日期：{{ thisData() }}</div>
        <router-link to="/english/home">
          <div class="multilingual">English</div>
        </router-link>
      </div>
    </header>
    <div class="banner">
      <div class="banner-logo">
        <img src="@/assets/images/LOGO_White.png" alt="">
      </div>
    </div>
    <ul class="nav">
      <li v-for="value in top_level" :key="value.uid"><a :href="value.links">{{ value.name }}</a></li>
    </ul>
    <router-view></router-view>
  </div>
</template>

